$tile-size: 28px;

.tile {
  background-color: transparent;
  border-radius: 2px;
  border-style: none;
  height: $tile-size;
  width: $tile-size;

  &:focus {
    outline: 0;
  }
}

.i {
  background-color: #0dc2ff;
}

.j {
  background-color: #3877ff;
}

.l {
  background-color: #ff8e0d;
}

.o {
  background-color: #ffe138;
}

.s {
  background-color: #0dff72;
}

.t {
  background-color: #f538ff;
}

.z {
  background-color: #ff0d72;
}
